.schedule {
    &__title {
       font-family: Montserrat;
       font-style: normal;
       font-weight: bold;
       font-size: 36px;
       line-height: 44px;
       
       /* identical to box height */
       
       color: #010001;
    }

    &__table thead tr {
        font-family: Montserrat;
font-size: 18px;
line-height: 22px;

color: #B6B6B6;


    }

    &__table tbody td {
        font-family: Montserrat;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;

        color: #000000;


    }

    &__table thead td {
        padding: 15px;
    }
    
    &__table tbody td {
        padding: 15px;
    }

    &__table tbody tr:nth-of-type(even) {
        background: #F5F5F5;
        border-radius: 5px;
    }
}